import React, { useState, useRef, useEffect } from "react";
import { EventAPI } from "../../commons/EventAPI";
import wrapper from "../../commons/WebComponentWrapper";
import { library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

/*
    Example: <srs-icon name="Add" description="Add an item"/>
*/

const dispatchedEvents = [EventAPI.ICON_CLICKED];

const SrsIcon = function (props, ref) {
    const [iconInstance, setIconInstance] = useState(null);

    useEffect(() => {
        if(!props.type || props.type == "carbon") {
            if(props.name) {
                import('@carbon/icons-react').then((iconLibrary)=> {
                    setIconInstance(iconLibrary[props.name]);
                });
            }
        }else if(props.type == "fontawesome") {
            import("@fortawesome/free-solid-svg-icons").then((iconLibrary) => {
                let iconNameParts = props.name.split("-");
                iconNameParts = iconNameParts.map((iconNamePart) => iconNamePart[0].toUpperCase() + iconNamePart.substring(1));
                let moduleName = "fa" + iconNameParts.join("");
                setIconInstance(iconLibrary[moduleName]);
                library.add(iconLibrary[moduleName]);
            });            
        }
    }, [props.name, props.type]);
    
    let inlineStyles = {
       color: props.color ||(props.theme == "white" || props.theme == "g10"? "black": "white"),
       "--cds-icon-primary": props.color ||(props.theme == "white" || props.theme == "g10"? "black": "white")
    };

    const handleClick = (props) => {
        return (evt) => {
            props.dispatchEvent && props.dispatchEvent(EventAPI.ICON_CLICKED, {name: props.name, id: props.elementid})
            if(props.onClick) {
                props.onClick({name: props.name, id: props.elementid});
            }
        }
    }

    let iconEl = null;
    if(!props.type || props.type == "carbon") {
        let IconInstance = iconInstance;
        iconEl = <IconInstance size={props.size || 24} onClick={handleClick(props)}/>;
    }else if(props.type == "fontawesome") {
        iconEl = <FontAwesomeIcon style={{cursor: "pointer", height: props.height || props.size, width: props.width || props.size, color: props.color}} 
                    onClick={handleClick(props)}
                    title={props.description}
                    icon={props.name}/>
    }

    return iconInstance? <div style={inlineStyles}>
            { iconEl } 
        </div>: <div/>
}

const styles = `

`
const props = [{
    name: "name",
    defaultValue: "",
    type: "string"
}, {
    name: "description",
    defaultValue: true,
    type: "boolean"
}, {
    name: "size",
    defaultValue: 24,
    type: "number"
}, {
    name: "color",
    defaultValue: "",
    type: "string"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsIcon), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
export default SrsIcon;